@import url('https://fonts.googleapis.com/css?family=Dancing+Script');

.App {
  text-align: center;
}

.App h1,
.App h2 {
  color: #444;
  font-family: 'Dancing Script', serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  overflow: hidden;
  background-color: rgb(255, 208, 0);
  height: 150px;
  padding: 0px;
  color: rgb(44, 44, 44);
  position: relative;
}

.App-section {
  padding: 20px 0;
}

.App-title {
  font-size: 2.0em;
  font-weight: 100;
  font-family: 'Dancing Script', sans-serif;
}
.App-title-desc {
  font-size: 1.0em;
  color: #777;
}

.App-intro {
  font-size: large;
}

.App-github-link {
  position: absolute;
  top: 0;
  right: 0;
}
.App-github-link-decor {
  position: absolute;
  top: -55px;
  right: -55px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  background-color: #fff;
  width: 100px;
  height: 100px;
}
.App-github-link svg {
  width: 30px;
}
.App-proj-item {
  margin: 10px 0;
  padding: 20px;
  color: #777;
  /* background-color: #0af; */
}

.App-refs-item-desc {
  font-size: 0.9em;
  color: #777;
}
.App-refs-item-btn {
  font-size: 0.7em;
}
.App-refs-item-name {
  color: #444;
}

.App-skill-item {
  margin: 10px 0;
}
.App-skill-item-name {
  color: #777;
}
.App-skill-item svg {
  width: 40px;
}
.App-skill-item p {
  margin: 5px 0;
  font-size: 0.8em;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (min-width: 576px) {
  .App-title {
    font-size: 2.5em;
  }
}


